import React, { Component } from 'react';

class ThankYou extends Component {
  static async getInitialProps() {
    return {data: "Some data"}
  }
  render() {
    return (
      <h1>Thank you! {this.props.data}</h1>
    );
  }
}

export default ThankYou;



